    let module = () => {
            fetch('https://38.svetacdn.in/api/translations?api_token=caPViSv7B4vYtQeyX0bAAdcHxSOQSc1O')
                .then(response => response.json())
                .then((function(result) {
                    translations = result
                    loadVideos()
                    loading = false
                })
            )}
     module()
    async function loadVideos() {
         try { 
            let res = await fetch('https:'+videos);
            let filmsPage = await res.text();
            if(res.ok) {
                let parser = new DOMParser();
                let doc = parser.parseFromString(filmsPage, 'text/html');
                let files = doc.getElementById("files").value
                let jsonFilm = files.replaceAll('\\', '')
                let filmObject = JSON.parse(jsonFilm);
                
                function translation(id) {
                    let result = translations.data
                    return result.filter(x => x.id == id).map(x => x.short_title)[0]
                }

                const checkurl = () => {
                    Object.entries(filmObject).forEach(element => {
                        let id = element[0]

                        let strValue = element[1]
                        strValue = strValue.replaceAll(' or //', ',')
                        strValue = strValue.split(',')
                        strValue = strValue[13]
                        let str = strValue?.slice(0, -7)
                        src = [...src, {'id': id, 'src': str, 'translation': translation(id)}]
                        src=src
                    });
                    resolution = qualities[selected][0]
                }
                checkurl()
            }
        } catch (error) {
            console.error('Ошибка:', error);
        }
    }

     let session = null

    let remotePlay = () => {
        window['__onGCastApiAvailable'] = function(isAvailable) {
            if (isAvailable) {
                initializeCastApi();
            }
        };

        let initializeCastApi = () => {
            let applicationID = chrom.cast.media.DEFAULT_MEDIA_RECEIVER_APP_ID
            let sessionRequest = new chrome.cast.sessionRequest(applicationID)
            console.log(sessionRequest)
            let apiConfig = new chrome.cast.apiConfig(sessionRequest, sessionListener, receiveListener)
            chrome.cast.initialize(apiConfig,onInitSuccess, onInitError)
        }

        let sessionListener = (e) => {
            session = e
            console.log('New session')
            if(session.media.length !=0) {
                console.log('Found '+session.media.length+' session')
            }
        }
        let receiverListener = (e) => {
            if(e === 'available') {
                console.log('Chromecast was found on the network.')
            } else {
                console.log('There are not Chromecast available.')
            }
        }
        let onInitSuccess = () => {
            console.log('Initialization succesed')
        }
        let onInitError = () => {
            console.log('Initialization failed');
        }
        let launchApp = () => {
            console.log('Launching the Chromecast App...')
            chrome.cast.requestSession(onRequestSessionSuccess, onLaunchError)
        }
        let onLaunchError = () => {
            console.log('Error connecting to the Chromecast.');
        }
        let onRequestSessionSuccess = (e) => {
            console.log('Successfully created session: ' + e.sessionId );
            session = e
            loadMedia()
        }
        let loadMedia = () => {
            if(!session) {
                console.log('No session.')
                return
            }
            let mediaInfo = new chrome.cast.mdeia.MediaInfo('https://i.imgur.com/IFD14.jpg')
            mediaInfo.contentType = 'image/jpg'
            let request = new chrome.cast.media.LoadRequest(mediaInfo)
            request.autoplay = true
            session.loadMedia(request, onLoadSuccess, onLoadError)
        }
        let onLoadSuccess = () => {
            console.log('Successfully loaded image.')
        }
        let onLoadError = () => {
            console.log('Failed to load image.')
        }
        let stopApp = () => {
            session.stop(onStopAppSuccess, onStopAppError)
        }
        let onStopAppSuccess= () => {
            console.log('Successfully stopped app.');
        }
        let onStopAppError = () => {
            console.log('Error stopping app.');
        }
    }


